.panel {
  animation: portal-login 1s forwards 1;
  animation-delay: 0.6s;
  transform: translateY(20px);
  opacity: 0;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 24px;
}

.submit_btn {
  width: 100%;
  border-radius: 40px;
  margin-top: 20px;
  margin-bottom: 20px;
  color: #fff;
}

.card-content {
  border-bottom: 2px solid rgba(0, 0, 0, 0.37);
}

.is-invalid fieldset {
  border-color: #f50057;
}

.is-invalid span span {
  color: #f50057;
}

.invalid-feedback {
  font-size: 14px;
  color: #f50057;
}

.inline-element {
  margin-left: 0px;
  margin-right: 0px;
  display: inline-block;
}

.continue_btn {
  border-radius: 40px;
  margin-top: 20px;
  color: #fff;
  padding: 12px 30px;
}

.form_visible {
  margin-top: 15px;
  display: block;
}

.form_invisible {
  display: none;
}

.next_container {
  border-radius: 40px;
  margin-top: 20px;
  color: #fff;
  padding: 12px 30px;
  float: right;
}

.process_btn {
  position: absolute;
}

@keyframes portal-login {
  0% {
    transform: translateY(20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
